/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/

/* Buttons */

button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
	cursor: pointer;
	border: 1px solid black;
	color: black;
	padding: 0.75rem 1.5rem;
	background-color: white;
	font-family: $font__header;
	font-size: 1rem;
	text-transform: uppercase;
	
	&:hover, &:active, &:focus {
		background-color: $color__text-screen;
		border-color: $color__text-screen;
		color: white;
	}
}

/* Fields */

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
textarea {
	color: #666;
	border: 1px solid #ccc;
	border-radius: 3px;

	&:focus {
		color: #111;
	}
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"] {
	padding: 3px;
}

textarea {
	padding-left: 3px;
	width: 100%;
}

// Customize search form
.search {
	&-form {
		label {
			@include flex-items-content(center, flex-start);

			.search {
				&-submit {
					cursor: pointer;
					padding: 0;
					border: 0 solid transparent;
					color: $color__text-screen;
					&:hover, &:active, &:focus {
						background-color: transparent;
						border-color: none;
						color: $color__link-hover;
					}
				}
				&-field {
					width: 0;
					padding: 0;
					text-transform: inherit;
					font-family: inherit;
					margin-left: 1rem;
					border: 0 solid transparent;
					transition: .4s;
					&.expand {
						padding: 5px;
						width: 12rem;
					}
				}
			}
		}
	}
}